//imports 
import React, {useState} from 'react';

function AboutMe() {

  const [visible, setVisible] = useState(true);

  const toggle = () => {
    setVisible(!visible);
  }

  
  return (


<div className="content">
        <div className="title">
            <h1>About me</h1>
        </div>
        <div className="body">
          <div className="space-between top">
          <div id="statement">
              <h2>Summary</h2>
              <ul>
                <li>I am an IT Manager with 7 years of experience in designing and implementing technology enabled business solutions.</li>
                <li>I have a business degree supported by a IT certificates in managing projects, technology products and software quality.</li>
                <li>I have experience in the following:</li>
                  <ul>
                    <li><span className="bold">roles:  </span>Business Analyst, Product Owner, Test Manager and Project Manager.</li>
                    <li><span className="bold">projects:  </span>IT strategy, pre-implementation analysis, software design and implementation.</li>
                    <li><span className="bold">markets:  </span>UK (London), Switzerland (Zurich), Netherlands (Amsterdam) and Poland (Warsaw).</li>
                    <li><span className="bold">sectors:  </span>transportation, banking and utilities.</li>
                  </ul>
              </ul>


          </div>
          <div id="info">
            <h2>Personal Information</h2>
            <p><span className="bold">Name : </span>Piotr Żakowicz</p>
            <p><span className="bold">Location : </span>Warsaw, Poland</p>
            <p><span className="bold">Email : </span>piotr.zakowicz@gmail.com</p>
            <div className="flex">
              <span className="bold">Get in touch : </span>
              <a href="https://www.linkedin.com/in/piotr-%C5%BCakowicz-31550955/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin fa-2x"></i></a>
              <a href="mailto: piotr.zakowicz@gmail.com" target="_blank" rel="noopener noreferrer"><i className="fas fa-envelope fa-2x"></i></a>
            </div>
            {visible ? 
            <button className="download" onClick={toggle}>DOWNLOAD CV</button>
            : <div className="flex">
              <a href="Piotr_Żakowicz_CV_PL.pdf" target="_blank" rel="noopener noreferrer"><button className="download sub-button" onClick={toggle}>POLISH</button></a>
              <a href="Piotr_Żakowicz_CV_ENG.pdf" target="_blank" rel="noopener noreferrer"><button className="download sub-button" onClick={toggle}>ENGLISH</button></a>
            </div>}

          </div>
        </div>
          <h2>Obejctive</h2>
          <h4 id="mission">I am currently studying and looking for a position as a JavaScript developer.</h4>
          <p>You can find more details about my skills and projects in CV and portfolio sections of this website.</p>

        </div>

    </div>
  )


}

export default AboutMe;