//imports 
import React from 'react';

function Certificate({name, school, location, date, img}) {
  
  return (
    <div className="certificate-container ">


      <div className="certificate flex">
        <p className="date2">{date}</p>
        <div className="certification">
          <h3 className="cert-name">{name}</h3>
          <h5 className="school">{school}, {location}</h5>
        </div>
        <img src={img} alt={school + "logo"} className={school + " logo"}/>
      </div>
    </div>
  )
}

export default Certificate;