//imports 
import React from 'react';
import Certificate from './Certificate';
import {certificates} from '../../../data';


function Education() {
  
  return (
    <div id="certificates" className="timeline2">
      {certificates.map((cert, j) => 
          <Certificate 
          key={j}
          name={cert.name}
          school={cert.school}
          location={cert.location}
          date={cert.date}
          img={cert.img}
          />
      )}
      
    </div>
  )
}

export default Education;