//imports 
import React from 'react';

function CVWebsite() {
  
  return (
<div className="content">
            <div className="title">
                  <h1>CV Website</h1>
            </div>
            <h3>Features</h3>
            <ul>
                  <li>Personal summary</li>
                  <li>CV</li>
                  <li>Software project portfolio</li>

            </ul>

            <h3>Technology</h3>
            <h4>Fronted</h4>
            <p>The fronted has been developed using React. Key dependencies and technologies include:</p>
            <ul>

                  <li>Create react app - used to create the initial template of the app. React scritps are used for compiling dev and production builds.</li>
                  <li>React router - used for routing components corresponding to separate pages.</li>
                  <li>React transition group - used to create a sliding transition for positions on the CV page.</li>
                  <li>Font awesome - used to provide icons.</li>
                  <li>Gh-pages - used for deploying the app to Github pages.</li>
            </ul>


      </div>
  )


}

export default CVWebsite;