//imports 
import React, {useState} from 'react';
import Project from './Project';
import { CSSTransition } from 'react-transition-group';

function Role({position, department, company, start, end, projects, image}) {

  const [visible, setVisible] = useState(false);



  const toggleVisible = () => {
    setVisible(!visible);

  }


  
  return (

    <div className="role-container timeline">
      <p className="date timeline-background">{end}</p>
      <div className="role">
        <div className="role-header space-between" onClick={toggleVisible}>
          <div className="center">
          {visible ? <i className="fa fa-chevron-up" ></i> : <i className="fa fa-chevron-down"></i> }
          <div className="role-header">
            <h3 className="position">{position}</h3>
            <h5 className="company">{company}</h5>
          </div>
        </div>
          <img src={image} alt="logo" className={company + " logo"}/>
        </div>
          <CSSTransition
            in={visible}
            timeout={1000} 
            classNames="slide"
            unmountOnExit
          >


          <div className="slide">


      
            <h4 className="key-projects">Key projects:</h4>
        
              {projects.map((project, i) => 

                <Project 
                key={i}
                name={project.name}
                client={project.client}
                role={project.role}
                duration={project.duration}
                responsibilities={project.responsibilities}
                />
                )}

          </div> 
         </CSSTransition>

      </div>
      <p className="date">{start}</p>
      
    </div>


  )


}

export default Role;