//imports 
import React from 'react';

function Lyftbros() {
  
  return (
      <div className="content">
            <div className="title">
                  <h1>Lyftbros</h1>
            </div>
            <div className="details-container body">
                  <div id="text">
                        <h2 className="heading">Features</h2>
                        <ul>
                              <li>Create account and login</li>
                              <li>Log a workout </li>
                              <li>Workout feed - a summary of recent workout logged by friends</li>
                              <li>Summary stats of a given user for the past 4 weeks, incl. number of workouts and workout volume</li>
                              <li>Details of a given workout</li>
                              <li>Personal Records - users receive trophies for top3 all time lifts, summaried on their page</li>
                              <li>Fist-bumps and comments can be posted on workouts</li>
                              <li>Push notifications are sent to users when a workout has been fist-bumped or commented on.</li>
                              <li>Account details with an option to edit</li>
                        </ul>

                        <h2 className="heading">Technology</h2>
                        <h3 className="subheading">Front-end</h3>
                        <p>The fronted has been developed using React. Key dependencies and technologies include:</p>
                        <ul>
                              <li>Redux - used for global state management.</li>
                              <li>Redux thunk - used to fetch data from the database asynchronously outside of the component lifecycle.</li>
                              <li>Redux persist - used to persist the redux store to retain data about an in progress workout and to ensure a quick initial render, while latest data is being fetch from the database.</li>
                              <li>Webpack - used together with Babel to transpile the React code to pure JavaScript to be run in the browser. Terser plugin is used production builds.</li>
                              <li>Service worker - used to cache image assets in the cache and retrieve them using network falling back on cache strategy.</li>
                              <li>Webpush - used together with the service worker to send push notifications to subscribed devices. Subscriptions are stored in the database together with user ids to allow for sending notification to users on multiple devices.</li>
                              <li>Font awesome - used to icons.</li>

                        </ul>

                        <h3 className="subheading">Back-end</h3>
                        <p>The backend has been developed using Node.js and Express.js. Key dependencies include:</p>
                        <ul>
                              <li>Express - used to run the server, handle the middleware and provide routing for the API.</li>
                              <li>Mysql2 - used by the API to open and close connections with the MySQL database.</li>
                              <li>Passport - used together with express-session to authenticate users. Usernames and password are hashed using bcrypt and stored together with the session id in the database.</li>
                              <li>Dotenv - used for storing environment variables for the development environment.</li>
                        </ul>

                        <p className="subheading">Previously used dependencies:</p>
                        <ul>
                              <li>EJS - view engine used for server-side rendering together with Express router. Removed in favour of client-side rendering with React and React Router allowing for better offline performace and speed after initial load.</li>
                              <li>SQLite - replaced by MySQL during the deployment to cloud, as Heroku's file system gets wiped daily.</li>
                        </ul>
                  </div>
                  <div>
                        <img src="../iphone_x_feed3.svg" alt="iphone feed" className="screen" />

                        <img src="../iphone_x_log.svg" alt="iphone feed" className="screen" />

                  </div>
            </div>

      </div>
  )


}

export default Lyftbros;