//imports 
import React, {useState} from 'react';
import Button from './Button/Button';



function NavBar({active}) {

  const [activeButton, setActiveButton] = useState(active);

  const toggleButtons = (id) => {
    setActiveButton(id);
  }

  
  return (

  <div className="nav">

    <div id="menu">
      <img src={require("./profile2.jpg")} alt="Profile pic"/>
        <div className="button" id="name">
            <p>PIOTR ŻAKOWICZ</p>
        </div>
        <Button url="/" name="About" id="1" icon="far fa-user fa-2x" active={activeButton} toggleButtons={toggleButtons}/>
        <Button url="/cv" name="CV" id="2" icon="far fa-address-card fa-2x" active={activeButton} toggleButtons={toggleButtons}/>
        <Button url="/portfolio" name="Portfolio" id="3" icon="fa fa-github fa-2x" active={activeButton} toggleButtons={toggleButtons}/>          
    </div>
    <h5 id="call-to-action">Get in touch</h5>
    <div className="flex" id="contact-buttons">
      <a href="https://www.linkedin.com/in/piotr-%C5%BCakowicz-31550955/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
      <a href="mailto: piotr.zakowicz@gmail.com" target="_blank" rel="noopener noreferrer"><i className="fas fa-envelope"></i></a>
    </div>
  </div>
  )


}

export default NavBar;