//imports 
import React from 'react';
import {Link} from 'react-router-dom';

function Project({name, logo, summary, link, github, url}) {
  
  return (
        <div className="project-container">
          <div className="flex">
            <h2>{name}</h2> 
            {logo ? <img className="portfolio-image" src={logo} alt="Project logo"/> : null}
          </div>
            <p className="summary">{summary}</p>
            <div className="space-between button-container">
              <a href={link} target="_blank" rel="noopener noreferrer" className="link"><button className="project-button">Visit</button></a>
              <br />
              <a href={github} target="_blank" rel="noopener noreferrer" className="link"><button className="project-button">Repository</button></a>
              <br />
              <Link to={"portfolio/" + url}><button className="project-button">Details</button></Link>
            </div>

        </div>
  )


}

export default Project;