//imports 
import React, {useState} from 'react';
import { CSSTransition } from 'react-transition-group';

function Project({name, role, duration, responsibilities, client}) {

  const [visible, setVisible] = useState(false);

  const toggleVisible = () => {
    setVisible(!visible);
  }
  
  return (



    <div className="project">
      <div className="header subheader" onClick={toggleVisible}>
        {visible ? <i className="fa fa-chevron-up" ></i> : <i className="fa fa-chevron-down"></i> }
        <div>
          <h4>Project: {name}</h4>
          <h5>Client: {client}</h5>
          <h5>Role: {role}</h5>
        </div>
      </div>
      <CSSTransition
            in={visible}
            timeout={1000} 
            classNames="slide"
            unmountOnExit
          >

        <div className="project-content slide">
          <h5 className="responsibilities">Key responsibilities:</h5>
          <ul>
            {responsibilities.map((res, i) => 
            <li key={i}>{res}</li>
            )}
          </ul>
        </div>
      </CSSTransition>
    </div>




  )


}

export default Project;