//imports 
import React from 'react';
import Role from './Role';
import {roles} from '../../../data';

function Experience() {

 
  
  return (

    <div >
      {roles.map((role, i) => 
        <Role 
        key={i}
        position={role.position}
        department={role.department}
        company={role.company}
        start={role.start}
        end={role.end}
        projects={role.projects}
        image={role.image}
        />
      )}
    </div>
  )
}

export default Experience;